import React, {useEffect} from 'react';
import {Link, useLocation, useParams} from "react-router-dom";
import textResultAgree from "../utils/textResultAgree";
import goodRobot from '../../img/goodRobot.png'
import evilRobot from '../../img/evilRobot.png'
import './index.scss'
import img1 from "../../img/main/1.png";
import img2 from "../../img/main/2.png";
import img3 from "../../img/main/3.png";
import img4 from "../../img/main/4.png";
import img5 from "../../img/main/5.png";
import img6 from "../../img/main/6.png";
import img7 from "../../img/main/7.png";
import img8 from "../../img/main/8.png";
import img9 from "../../img/main/9.png";
import img10 from "../../img/main/10.png";
import img11 from "../../img/main/11.png";
import img12 from "../../img/main/12.png";
import img13 from "../../img/main/13.png";
import img14 from "../../img/main/14.png";
import img15 from "../../img/main/15.png";
import mainPage from "../../img/main/main.png";
import {useCounterStore} from "../state/NumberProvider";


const ResultBody = () => {
    const {id} = useParams()
    const location = useLocation();
    const levelId = Number(id);
    const {increment, decrement} = useCounterStore()
    const imgRender = (): string => {
        switch (levelId) {
            case (1):
                return img1
            case (2):
                return img2
            case (3):
                return img3
            case (4):
                return img4
            case (5):
                return img5
            case (6):
                return img6
            case (7):
                return img7
            case (8):
                return img8
            case (9):
                return img9
            case (10):
                return img10
            case (11):
                return img11
            case (12):
                return img12
            case (13):
                return img13
            case (14):
                return img14
            case (15):
                return img15
            default :
                return mainPage
        }
    }

    const mapResult = [6, 9, 12, 13]
    const navigate = () => {
        if (levelId === 10) {
            return '/interruption'
        } else if (levelId === 15) {
            return '/total'
        } else if (levelId ===4) {
            return '/facts/4'
        } else if (levelId ===8) {
            return '/facts/8'
        } else if (levelId ===11) {
            return '/facts/11'
        } else if (levelId ===14) {
            return '/facts/14'
        } else {
            return `/game/${levelId + 1}`
        }
    }
    const answer = () => {
        if ((location.pathname).indexOf('dontAgree') === 1) {

            return mapResult.includes(levelId) ? " " : "  "
        } else {
            return mapResult.includes(levelId) ? "  " : " "
        }
    }
    const point = () => {
        if ((location.pathname).indexOf('dontAgree') === 1) {
            console.log(222)
            return mapResult.includes(levelId) && increment()
        } else {
            console.log(111)
            return !mapResult.includes(levelId) && increment()
        }
    }

    useEffect(() => {
        point()
    }, [])
    return (
        <div className={'resultBody'}>
            <div className={'containerResult'}>
                <div className={'answer'}>{answer()}</div>
                <div className={'bodyResult'}>

                    <span>{textResultAgree[levelId - 1]}</span>
                    <img src={imgRender()}/>

                </div>
            </div>
            <Link to={navigate()}>
                <button className={'next'}></button>
            </Link>
        </div>
    );
};

export default ResultBody;
