import React from 'react';
import textResultAgree from "../utils/textResultAgree";
import {Link, useLocation, useParams} from "react-router-dom";
import './index.scss'
import fact1 from '../../img/main/fact1.png'
import fact2 from '../../img/main/fact2.png'
import fact3 from "../../img/main/14.png";
import fact4 from "../../img/main/10.png";
import textFactsArray from "../utils/factsTest";

const Facts = () => {

    const {id} = useParams()
    const location = useLocation();
    const levelId = Number(id);
    const imgRender = (): string => {
        switch (levelId) {
            case (4):
                return fact1
            case (8):
                return fact2
            case (11):
                return fact3
            case (14):
                return fact4
            default :
                return fact1
        }
    }
    const factText = ()=>{
        switch (levelId) {
            case (4):
                return textFactsArray[0]
            case (8):
                return textFactsArray[1]
            case (11):
                return textFactsArray[2]
            case (14):
                return textFactsArray[3]
            default :
                return textFactsArray[0]
        }
    }
    return (
            <div className={'factsBody'}>
                <div className={'containerResult'}>
                    <div className={'answer'}>   </div>
                    <div className={'bodyResult'}>

                        <span>{factText()}</span>
                        <img src={imgRender()}/>

                    </div>
                </div>
                <Link to={`/game/${levelId+1}`}>
                    <button className={'next'}></button>
                </Link>
        </div>
    );
};

export default Facts;
