import create from 'zustand';

type CounterState = {
    count: number;
    increment: () => void;
    decrement: () => void;
};

export const useCounterStore = create<CounterState>((set:any) => ({
    count: 0,
    increment: () => set((state:any) => ({ count: state.count + 1 })),
    decrement: () => set((state:any) => ({ count: state.count - 1 })),
}));
