import React from 'react';
import {Route, Routes} from "react-router-dom";
import MainPage from "../../pages/MainPage";
import Game from "../../pages/Game";
import Result from "../../pages/Result";
import InterruptionPage from "../../pages/InterruptionPage";
import FinalPage from "../../pages/FinalPage";
import PreviewPage from "../../pages/PreviewPage";
import TotalPointsPages from "../../pages/TotalPointsPages";
import InterruptionFactsPage from "../../pages/InterruptionFactsPage";

const AppRouter = () => {
    return (
       <>
           <Routes>
               <Route path={'/'} element={<MainPage/>}/>
               <Route path={'/preview'} element={<PreviewPage/>}/>
               <Route path={'/game/:id'} element={<Game/>}/>
               <Route path={'/agree/:id'} element={<Result/>}/>
               <Route path={'/dontAgree/:id'} element={<Result/>}/>
               <Route path={'/interruption'} element={<InterruptionPage/>}/>
               <Route path={'/final'} element={<FinalPage/>}/>
                <Route path={'/total'} element={<TotalPointsPages/>}/>
                <Route path={'/facts/:id'} element={<InterruptionFactsPage/>}/>

           </Routes>
       </>
    );
};

export default AppRouter;
