import React, {useEffect} from 'react';
import {useCounterStore} from "../../components/state/NumberProvider";
import ScrollToTop from "../../components/utils/ScrollToTop";
import Header from "../../components/Header";
import Dragon from "../../img/dragon.png";
import './indes.scss'
import {Link} from "react-router-dom";
const TotalPointsPages = () => {
    const {count} = useCounterStore()
    useEffect(()=>{
        document.body.style.overflow='hidden'
    },[])
    ScrollToTop()
    window.addEventListener('resize', () => {
        //    
        let vh = window.innerHeight * 0.01;
        document.documentElement.style.setProperty('--vh', `${vh}px`);
    });
    let maxHeight = window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight;
    return (

        <div className="totalPageContainer" style={{maxHeight: maxHeight}}>
            <Header/>

            <div className="mainText">
                <div className={'result'}> :</div>
                <div className={'count'}>{count/2}/15</div>
                <Link to={'/final'}>
                <button> !</button>
                </Link>
            </div>
            <div className="imgDragon">
                <img className={'dragon'} src={Dragon }/>

            </div>
        </div>
    );
};

export default TotalPointsPages;
